<template>
  <!--<index-layout selected="mine">-->
  <keep-alive>
    <div v-if="isLogin">
      <div class="review">
        <div class="title"><span v-text="username"></span></div>
        <hr>
        <div class="content" style="text-indent: 0" @click="toNewIntro"><span v-text="selfIntro"></span></div>
      </div>
      <div class="button-outside">
        <mt-button type="default" size="large" @click="toMyArticles"></mt-button>
      </div>
      <div class="button-outside">
        <mt-button type="default" size="large" @click="toMyEssays"></mt-button>
      </div>
      <div class="review">
        <div class="title"></div>
        <hr>
        <div class="content">
          <p>(<em>Six Degrees of Separation</em>)</p>
          <p>,,<em>""</em></p>
          <p><b>
            <router-link to="/about"></router-link>
          </b>
          </p>
        </div>
      </div>
      <div class="button-outside">
        <mt-button type="primary" size="large" @click="toPasswd"></mt-button>
      </div>
      <div class="button-outside">
        <mt-button type="default" size="large" @click="logout"></mt-button>
      </div>
    </div>
    <div v-else>
      <div class="review">
        <div class="title"></div>
        <hr>
        <div class="content">
          <p>(<em>Six Degrees of Separation</em>)</p>
          <p>,,<em>""</em></p>
          <p><b>
            <router-link to="/about"></router-link>
          </b>
          </p>
        </div>
      </div>
      <div class="review">
        <b></b><b></b>
      </div>
      <div class="button-outside">
        <mt-button type="primary" size="large" @click="toLoginPage"></mt-button>
      </div>
      <div class="button-outside">
        <mt-button type="default" size="large" @click="toRegisterPage"></mt-button>
      </div>
      <hr>
    </div>
  </keep-alive>
  <!--</index-layout>-->
</template>

<script>
  // import IndexLayout from '../components/IndexLayout';
  import ArticleReview from '../components/ArticleReview';
  import {Indicator} from 'mint-ui';
  import {Toast} from 'mint-ui';

  export default {
    components: {
      // IndexLayout,
      ArticleReview
    },
    name: "MinePage",
    data() {
      return {
        articleList: [],
        essaysList: [],
        loading: true,
        // url: 'http://127.0.0.1:5000',
        // isLogin: false,
        selfIntro: ''
      }
    },
    computed: {
      isLogin() {
        return this.$store.state.isLogin
      },
      username() {
        return this.$store.state.username
      },
      userID() {
        return this.$store.state.userID
      },
      url() {
        return this.$store.state.url +'/api'+ this.$route.path
      },
    },
    methods: {
      toLoginPage() {
        this.$router.push('/login')
      },
      toRegisterPage() {
        this.$router.push('/register')
      },
      toMyArticles() {
        this.$router.push('/user_articles/' + this.userID)
      },
      toMyEssays() {
        this.$router.push('/user_essays/' + this.userID)
      },
      toNewIntro() {
        this.$router.push('/new_intro')
      },
      logout() {
        Indicator.open(',');
        this.axios.get(this.$store.state.url + '/api/logout')
          .then((resp) => {
            let temp = resp.data;
            this.$store.commit('logout');
            Indicator.close();
            if (temp.isLogout) {
              Toast('')
            } else {
              Toast('' + temp.failCode)
            }
          })
          .catch((err) => {
            console.log(err);
            Indicator.close();
            Toast('')
          })
      },
      toPasswd(){
        this.$router.push('/passwd')
      }
    },
    beforeMount() {
      if(this.isLogin){
        this.axios.get(this.url)
          .then((response) => {
            if (response.data.selfIntro !== '') {
              this.selfIntro = response.data.selfIntro;
            }
          })
          .catch((error) => {
            console.log(error);
            Toast('');
          });
      }
    }
  }
</script>

<style scoped>
  .review {
    margin: 15px;
    background: #fff;
    border-radius: 15px;
    padding: 20px;
    overflow: auto;
    box-shadow: darkgrey 10px 10px 20px 5px;
  }

  .button-outside {
    margin: 30px 15px;
    background: transparent;
    box-shadow: darkgrey 10px 10px 20px 5px;
    border-radius: 15px;
    font-size: 20px;
    line-height: 20px;
  }

  .title {
    font-size: 2.5em;
    color: rgba(46, 110, 206, 0.98);
  }

  .content {
    text-align: justify;
    text-indent: 2em;
  }
</style>



