<template>
  <div class="page">
    <mt-header :title="title">
      <mt-button icon="back" slot="left" style="line-height: 14px" @click="lastPage"></mt-button>
      <mt-button slot="right" @click="publish">&nbsp;<span class="glyphicon glyphicon-send" aria-hidden="true"></span>
      </mt-button>
    </mt-header>
    <div>
      <mavon-editor v-model="articleContent" :subfield="false" defaultOpen="edit" :toolbars="toolbars"
                    @imgAdd="$imgAdd" ref=mde></mavon-editor>
    </div>
    <mt-popup
      v-model="popupVisible"
      popup-transition="popup-fade"
      position="bottom">
      <div class="help" :style="alertWidth"><p>,</p>
        <p>Markdown</p>
        <p>300</p></div>
    </mt-popup>
  </div>
</template>

<script>
  import {MessageBox} from 'mint-ui';
  import {Toast} from 'mint-ui';
  import {Indicator} from 'mint-ui';

  export default {
    name: "NewEssayPage",
    data() {
      return {
        articleContent: '',
        title: '',
        toolbars: {
          bold: true, // 
          italic: true, // 
          header: true, // 
          underline: false, // 
          strikethrough: true, // 
          mark: false, // 
          superscript: false, // 
          subscript: false, // 
          quote: true, // 
          ol: true, // 
          ul: true, // 
          link: true, // 
          imagelink: true, // 
          code: true, // code
          table: true, // 
          fullscreen: false, // 
          readmodel: true, // 
          htmlcode: true, // html
          help: true, // 
          /* 1.3.5 */
          undo: true, // 
          redo: true, // 
          trash: true, // 
          save: false, // eventssave
          /* 1.4.2 */
          navigation: true, // 
          /* 2.1.8 */
          alignleft: false, // 
          aligncenter: false, // 
          alignright: false, // 
          /* 2.2.1 */
          subfield: true, // 
          preview: true, // 
        },
        popupVisible: false,
      }
    },
    computed: {
      isLogin() {
        return this.$store.state.isLogin
      },
      url() {
        return this.$store.state.url + '/api' + this.$route.path
      },

      innerW() {
        return window.innerWidth
          || document.documentElement.clientWidth
          || document.body.clientWidth;
      },
      alertWidth() {
        return 'width:' + this.innerW + 'px'
      },
    },
    watch: {
      articleContent: function (val) {
        if (val.length !== 0) {
          this.title = val.length + '/300';
          if (val.length > 300) {
            this.articleContent = this.articleContent.slice(0, 300);
            Toast({message: '', position: 'bottom'})
          }
        }
      }
    },
    methods: {
      lastPage() {
        if (this.articleContent !== '') {
          MessageBox.confirm(',?').then(action => {
            history.go(-1)
          }).catch(cancel => {
            Toast('')
          });
        } else {
          history.go(-1)
        }
      },
      publish() {
        if (this.articleContent !== '') {
          MessageBox.confirm('')
            .then(action => {
              Indicator.open('...');
              this.axios.post(this.url, {
                'articleContent': this.articleContent,
              })
                .then(resp => {
                  Indicator.close();
                  let temp = resp.data;
                  if (temp.isPublished) {
                    Toast('');
                    history.go(-1)
                  } else {
                    Toast("!" + temp.failCode)
                  }
                })
                .catch(err => {
                  console.log(err);
                  Indicator.close();
                  Toast('')
                })
            })
            .catch(cancel => {
              Toast('')
            });
        } else {
          MessageBox.alert('', '');
        }
      },
      $imgAdd(pos, $file) {
        // ..
        let formData = new FormData();
        formData.append('image', $file);
        this.axios({
          url: this.$store.state.url + '/api/upload_img',
          method: 'post',
          data: formData,
          headers: {'Content-Type': 'multipart/form-data'},
        }).then(resp => {
          // .url![...](0) -> ![...](url)
          // $vm.$img2Url 
          if (resp.data.success) {
            this.$refs.mde.$img2Url(pos, resp.data.path);
          } else {
            this.$refs.mde.$refs.toolbar_left.$imgDelByFilename($file.filename);
            Toast(':' + resp.data.failCode)
          }
        })
          .catch(err => {
            console.log(err);
            Toast('')
          })
      }
    },
    mounted() {
      setTimeout(() => {
        if (!this.isLogin) {
          Toast('');
          this.$router.push('/login')
        }
      }, 300);
      setTimeout(() => {
        this.popupVisible = true;
      }, 500);
      setTimeout(() => {
        this.popupVisible = false;
      }, 2000)
    }
  }
</script>

<style scoped>
  .page ul li {
    list-style: disc;
  }

  .help {
    padding: 15px 0;
    width: 100%;
    height: 180px;
    text-align: center;
    font-size: 1.3em;
    line-height: 50px;
  }
</style>



